.story-section {
	position: relative;
	z-index: 2;
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.orn-1 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 100px;
			transform: translate(-55%, 0);
			@include min-md {
				width: 140px;
			}
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(65%, 0);
			@include min-md {
				width: 200px;
				transform: translate(60%, 0);
			}
			@include min-lg {
				width: 240px;
			}
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
