.reminder-wrap {
	position: relative;
	display: block;
	padding: 2rem 0 5rem;
	overflow: hidden;
	@include max-md {
		padding: 2rem 0;
	}
	.orn-15 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translate(-40%, 30%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-16 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(40%, 30%) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 160px;
		transform: translate(-40%, 10%);
		@include min-xs {
			width: 200px;
		}
		@include min-md {
			width: 300px;
		}
		@include min-lg {
			transform: translate(-45%, 30%);
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(30%, 10%);
		@include min-xs {
			width: 140px;
		}
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			transform: translate(30%, 20%);
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 18%;
		left: 0;
		width: 100px;
		transform: translate(-30%, 0);
		@include min-xs {
			width: 120px;
		}
		@include min-md {
			bottom: 20%;
			width: 140px;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 100px;
			transform: translate(45%, -10%) rotate(10deg);
			@include min-xs {
				width: 120px;
				transform: translate(50%, -20%) rotate(5deg);
			}
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				transform: translate(50%, -10%) rotate(5deg);
			}
			.image-element {
				opacity: 0.8;
			}
			.orn-9 {
				.image-element {
					opacity: 1;
				}
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 20%;
		right: 0;
		width: 120px;
		transform: translate(50%, 0);
		opacity: 0.8;
		@include min-xs {
			width: 180px;
		}
		@include min-md {
			bottom: 22%;
			width: 220px;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			left: 0;
			width: 80px;
			transform: translate(-65%, -10%);
			@include min-xs {
				transform: translate(-55%, -10%);
			}
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 100px;
				transform: translate(-70%, 20%);
			}
			.image-element {
				opacity: 0.6;
			}
			.orn-9 {
				.image-element {
					opacity: 1;
				}
			}
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 120px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 180px;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 15%;
		left: 28%;
		width: 60px;
		transform: translate(-50%, 50%);
		@include min-lg {
			left: 35%;
			width: 80px;
		}
	}
	.orn-12 {
		position: absolute;
		top: 20%;
		left: 10%;
		width: 45px;
		transform: rotate(45deg);
		@include min-md {
			width: 60px;
		}
	}
	.orn-13 {
		position: absolute;
		top: 12%;
		right: 15%;
		width: 40px;
		transform: rotate(-45deg);
		@include min-md {
			width: 50px;
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		padding: 8rem 5rem;
		border-radius: 500px;
		overflow: hidden;
		@include max-md {
			padding: 5rem 3rem;
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
